import { createStore, applyMiddleware } from 'redux';
import { createEpicMiddleware } from 'redux-observable';
import {
  toggleFilterReducer,
  toggleDrawerReducer,
  toggleViewReducer,
  updateProviderData,
  submitProviderForm,
} from '../reducers';
import rootEpic from '../epics';
import { EMPTY_STATE } from './state';
import {
  getDateQuartersReducer,
  saveDateQuartersReducer,
} from '../reducers/date-quarters.reducer';

const ACTIONS = {
  TOGGLE_DRAWER: toggleDrawerReducer,
  TOGGLE_VIEW: toggleViewReducer,
  TOGGLE_FILTER: toggleFilterReducer,
  UPDATE_PROVIDER_DATA: updateProviderData,
  SUBMIT_PROVIDER_FORM: submitProviderForm,
  GET_DATE_QUARTERS: getDateQuartersReducer,
  SAVE_DATE_QUARTERS: saveDateQuartersReducer,
};

const ROOT_REDUCER = (state: any, action: any) => {
  if (process.env.NODE_ENV !== 'production') {
    console.log(state, action);
  }
  if (Object.keys(ACTIONS).indexOf(action.type) !== -1) {
    return ACTIONS[action.type](state, action);
  } else {
    return state;
  }
};

export default createStore(
  ROOT_REDUCER,
  EMPTY_STATE,
  applyMiddleware(createEpicMiddleware(rootEpic))
);



