import * as React from 'react';
import { withRouter } from 'react-router';
import styled from 'styled-components';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Header } from '../../components/AppShell/Header/Header';
import { AppRouter } from '../../components/Routes/AppRouter/AppRouter';
import { getDateQuarters } from '../../actions/date-quarters.action';

interface Props {
  getDateQuarters: () => {};
}

const Container = styled.div`
  text-align: center;
`;

class AppShellComponent extends React.Component<Props, any> {
  constructor(props: any) {
    super(props);
  }
  componentWillMount() {
    this.props.getDateQuarters();
  }
  render() {
    return (
      <Container>
        <Header />
        <AppRouter />
      </Container>
    );
  }
}

export const AppShell = withRouter(
  connect(
    state => state,
    dispatch => bindActionCreators({ getDateQuarters }, dispatch)
  )(AppShellComponent)
);



