import * as React from 'react';
import styled from 'styled-components';
import FlexView from 'react-flexview';
import {
  Accordion,
  AccordionItem,
  AccordionItemBody,
} from 'react-accordion-container';
import { PrimaryButton } from '../../Button/PrimaryButton';
import '../ToolCard/ToolCard.css';

const Arrow = require('../ToolCard/images/arrow.svg');
const Lock = require('../ToolCard/images/lock.svg');
const ToolCardFrame = styled.div`
  border: #555555 1px solid;
  padding: 20px;
  max-width: 294px;

  @media (max-width: 700px) {
    max-width: none;
    width: 100%;
  }
`;

const CardTitle = styled.h4`
  font-family: 'Fira Sans', sans-serif;
  font-size: 22px;
  margin: 0;
  padding-top: 10px;
  }
`;

const CardImage = styled.img`
  width: 100%;
  height: auto;
`;

const CardDescription = styled.p`
  font-family: 'Open Sans', sans-serif;
  text-align: left;
  margin: 0;
  font-size: 13px;
  padding-top: 10px;
`;

const AccordionTitle = styled.h5`
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  font-size: 13px;
  text-align: left;
  margin-top: 5px;
  margin-bottom: 5px;
  &:hover {
    cursor: pointer;
  }
`;
const AccordionDescription = styled.p`
  font-family: 'Open Sans', sans-serif;
  text-align: left;
  margin: 0;
  font-size: 13px;
`;

const AccordionLock = styled.img`
  max-width: 15px;
  margin-right: 10px;
`;

const AccordionText = styled.span`
  margin-bottom: 5px;
`;

const ArrowIcon = styled.img`
  position: 'relative';
  width: 15px;
  display: inline-block;
  transform: ${props => props.theme.main};
`;

ArrowIcon.defaultProps = {
  theme: {
    main: 'rotate(0deg)',
  },
};

const tags = ['Part A', 'Build Case', 'Collection Here'];

const AccordionTags = styled.div`
  background-color: #c9c9c9;
  padding: 5px;
  margin-right: 5px;
  margin-left: 5px;
  margin-bottom: 5px;
  font-size: 10px;
`;

const AccordionComponent = () => (
  <Accordion>
    <AccordionItem>
      {({ isActive, onClick }) => (
        <div>
          <AccordionTitle
            role="button"
            onClick={() => {
              onClick();
            }}>
            My first accordion item &nbsp;
            <ArrowIcon src={Arrow} className={`${isActive ? '' : 'rotate'}`} />
          </AccordionTitle>
          <AccordionItemBody>
            <AccordionDescription>
              <FlexView column={false} vAlignContent="top">
                <AccordionLock src={Lock} />
                <AccordionText>CMS ID Required for Access</AccordionText>
              </FlexView>
              <FlexView column={false} vAlignContent="top" wrap={true}>
                <AccordionText>Collections:</AccordionText>
                {tags.map(function(name: string, index: any) {
                  return <AccordionTags key="">{name}</AccordionTags>;
                })}
              </FlexView>
            </AccordionDescription>
          </AccordionItemBody>
        </div>
      )}
    </AccordionItem>
  </Accordion>
);

interface Props {
  imageUrl: string;
  summary: string;
  title?: string;
  tags?: string[];
}

export const ToolCard = ({ imageUrl, summary, title }: Props) => (
  <ToolCardFrame>
    <CardImage src={imageUrl} alt="" />
    <CardTitle>Tool Name Goes Here</CardTitle>
    <CardDescription>
      Lorem ipsum dolor sit amet, temporconsectetur adipiscing elit, sed do
      eiusmod tempor inci sed do codtur.....
    </CardDescription>
    <AccordionComponent />
    <FlexView column={false}>
      <FlexView marginRight={15}>
        <PrimaryButton text="Go To Tool" />
      </FlexView>
      <FlexView marginRight={15}>
        <PrimaryButton text="See Resources" />
      </FlexView>
    </FlexView>
  </ToolCardFrame>
);



