import * as React from 'react';
import ReactCardFlip from 'react-card-flip';
import styled from 'styled-components';
const Plus = require('../Collection/images/plus.svg');

const Front = styled.button`
  min-width: 250px;
  min-height: 250px;
  background-color: #01579b;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
`;

const FrontText = styled.h3`
  font-family: 'Fira Sans', sans-serif;
  font-size: 21px;
  color: #ffffff;
  width: 100%;
`;
const BackContainer = styled.div`
  padding: 20px;
`;
const BackText = styled.div`
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  color: #ffffff;
  width: 100%;
  text-align: left;
  margin-bottom: 20px;
`;
const PlusIcon = styled.img`
  max-width: 30px;
  height: auto;
`;

const Divider = styled.hr`
  width: 100%;
  margin-top: 15px;
  margin-bottom: 15px;
`;

const Back = styled.button`
  min-width: 250px;
  min-height: 250px;
  background-color: #002f6c;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  cursor: pointer;
`;
const ButtonLine = styled.div`
  width: 180px;
  height: 0px;
  background-color: #c49000;
  position: absolute;
  bottom: -2px;
  left: -2px;
`;
const BackButton = styled.button`
  position: relative;
  width: 180px;
  border: #ffffff 2px solid;
  color: #ffffff;
  background-color: transparent;
  border-radius: 0;
  padding: 10px;
  font-family: 'Fira Sans', sans-serif;
  font-size: 14px;

  &:hover {
    cursor: pointer;
    background-color: #ffffff;
    color: #222222;
    transition: all 0.5s ease;
  }
  &:hover ${ButtonLine} {
    height: 5px;
    transition: all 0.5s ease;
  }
`;

export default class App extends React.Component<{}, any> {
  constructor() {
    super({});
    this.state = {
      isFlipped: false,
    };
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e: any) {
    e.preventDefault();
    this.setState({ isFlipped: !this.state.isFlipped });
  }

  render() {
    return (
      <ReactCardFlip isFlipped={this.state.isFlipped}>
        <Front key="front" onClick={this.handleClick}>
          <div>
            <FrontText>Get a Case</FrontText>
            <Divider />
            <PlusIcon src={Plus} />
          </div>
        </Front>

        <Back key="back" onClick={this.handleClick}>
          <BackContainer>
            <BackText>
              Lorem ipsum dolor sit amet, tempor consectetur adipiscing elit,
              sed do eiusmod tempor inci sed do codtur ipsum dolor sit amet,
              tempor consectetur adipiscing elit, sed do eiusmod tempor inci sed
              do codtur do codtur ipsum dolor sit amet, tempor consectetur
            </BackText>
            <BackButton>
              Learn More
              <ButtonLine />
            </BackButton>
          </BackContainer>
        </Back>
      </ReactCardFlip>
    );
  }
}



