import * as React from 'react';
import { Switch, Route } from 'react-router-dom';
import { Landing as LandingComponent } from '../Landing/Landing';
import { ViewAll as ViewAllComponent } from '../ViewAll/ViewAll';
import { NotFound as NotFoundComponent } from '../NotFound/NotFound';
import { SingleTool } from '../SingleTool/SingleTool';

/** Note: purely declarative routing */
export const AppRouter = () => (
  <main>
    <Switch>
      <Route exact={true} path="/" component={LandingComponent} />
      <Route exact={true} path="/tool/:id" component={SingleTool} />
      <Route path="/tools" component={ViewAllComponent} />
      <Route component={NotFoundComponent} />
    </Switch>
  </main>
);



