export interface Quarters {
  to_qryr: string;
  from_qryr: string;
}

export const GET_DATE_QUARTERS = 'GET_DATE_QUARTERS';
export const getDateQuarters = () => ({
  type: GET_DATE_QUARTERS,
});

export const SAVE_DATE_QUARTERS = 'SAVE_DATE_QUARTERS';
export const saveDateQuarters = (quarters: Quarters[]) => ({
  type: SAVE_DATE_QUARTERS,
  payload: {
    rows: quarters,
  },
});



