import { ApplicationState } from '../interfaces';
import { Quarters } from '../actions/date-quarters.action';

export const getDateQuartersReducer = (state: ApplicationState) => state;

export const saveDateQuartersReducer = (
  state: ApplicationState,
  rows: { rows: Quarters[] }
) => ({
  ...state,
  quarters: rows,
});



