import { Quarters } from '../actions/date-quarters.action';

export enum ToolType {
  CDAC = 'CDAC',
  NONCDAC = 'Non-CDAC',
}

export interface Filter {
  key: string;
  text: string;
  value: boolean;
  parent?: string;
}

export interface Anchor {
  url: string;
  text: string;
}

export interface Highlight {
  name: string;
  text: string;
}

export interface Tool {
  short: string;
  type: ToolType;
  anchor: Anchor;
  description: string;
  supplements: Anchor[];
  highlights: Highlight[];
  filters: string[];
}

export enum DrawerStatus {
  CLOSED = 'CLOSED',
  OPEN = 'OPEN',
}

export enum ToolListView {
  TABLE = 'TABLE',
  CARDS = 'CARDS',
}

export interface ApplicationState {
  drawer: DrawerStatus;
  view: ToolListView;
  filters: Filter[];
  provider: Provider;
  claims: Claim[];
  form: ProviderForm;
  quarters: Quarters[];
}

export interface Provider {
  npi: number;
  name: string;
  location: string;
  speciality: string;
  taxId: number;
  ptanNumber: string;
}

export interface ActivitySummary {
  billedDirectly: number;
  paidDirectly: number;
  billedReferrals: number;
  paidReferrals: number;
  totalNumberOfClaims: number;
}

export interface Claim {
  provider_NPI: number;
  name: string;
  speciality: string;
  ptan_num: number;
  location: string;
  tax_id: number;
  claim_type: number;
  total_billed_directly: number;
  total_billed_by_referral: number;
  total_paid_directly: number;
  total_paid_by_referral: number;
  number_claims: number;
  number_visits: number;
  risk_score: number;
}

export interface ProviderForm {
  npinum: string;
  from: string;
  to: string;
}



