import { ProviderForm, Provider, Claim } from '../interfaces';
import { ApplicationState, DrawerStatus, ToolListView } from '../interfaces';

// toggleFilterReducer returns the ApplicationState with a payload's filter value inverted.
// i.e. if true it is returned false
export const toggleFilterReducer = (
  state: ApplicationState,
  { payload }: { payload: { filter: string } }
) => ({
  ...state,
  filters: state.filters.map(f => {
    if (payload.filter === f.key) {
      return { ...f, value: !f.value };
    }
    return f;
  }),
});

// toggleDrawerReducer returns the ApplicationState with Drawer inverted
export const toggleDrawerReducer = (state: ApplicationState) =>
  <ApplicationState>{
    ...state,
    drawer:
      state.drawer === DrawerStatus.OPEN
        ? DrawerStatus.CLOSED
        : DrawerStatus.OPEN,
  };

// toggleViewReducer returns the ApplicationState with view inverted
export const toggleViewReducer = (state: ApplicationState) => ({
  ...state,
  view:
    state.view === ToolListView.CARDS ? ToolListView.TABLE : ToolListView.CARDS,
});

// updateProviderForm updates the current Provider in focus in the ApplicationState
// based on form inputs handled via the providerFormEpic
export const updateProviderData = (
  state: ApplicationState,
  {
    payload: { provider, claims },
  }: { payload: { provider: Provider; claims: Claim[] } }
) => ({
  ...state,
  provider,
  claims,
});

// submitProviderForm submits a form inquiry that will be
// validated within epic.
export const submitProviderForm = (
  state: ApplicationState,
  { payload: { form } }: { payload: { form: ProviderForm } }
) => ({
  ...state,
  form,
});



