import { Store } from 'redux';
import { ActionsObservable } from 'redux-observable';
import * as request from 'superagent';
import { fromPromise } from 'rxjs/observable/fromPromise';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/mergeMap';
import {
  GET_DATE_QUARTERS,
  saveDateQuarters,
  Quarters,
} from '../actions/date-quarters.action';
import { DEFAULT_QUARTERS } from '../constants';

export const dateQuarterAggregatorEpic = (
  action$: ActionsObservable<any>,
  store: Store<any>
) =>
  action$
    .ofType(GET_DATE_QUARTERS)
    .map(() =>
      fromPromise(
        request
          .get(
            'https://&_srvnm/SASStoredProcess/do?_program=' +
              '%2FUser+Folders%2FMaria+Asencio%2FMy+Folder%2FOneHub%2FHUB_SP'
          )
          .catch(error => console.log(error))
      )
    )
    .mergeMap(response => response)
    .map(response => {
      console.log('RESPONSE', response);
      if (response) {
        return response.status === 200 ? response : DEFAULT_QUARTERS;
      }
      return DEFAULT_QUARTERS;
    })
    .map((response: Quarters[]) => saveDateQuarters(response));



