import * as React from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';
import FlexView from 'react-flexview/lib';
import { Icon } from 'react-fa';
const NIH = require('../Header/images/nih-ribbon.png');
const HHS = require('../Header/images/nihribbon-hhs.png');

const Banner = styled.div`
  color: white;
`;

const Title = styled.div`
  font-family: 'Fira Sans', sans-serif;
  color: white;
  font-size: 1.5em;
`;
const TopBar = styled.div`
  background-color: #002f6c;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: left;
`;

const NavBar = styled.div`
  background-color: rgba(1, 87, 155, 1);
  padding: 20px;
`;

const TopIcon = styled.img`
  width: 15px;
  height: 15px;
`;

const TopWords = styled.div`
  font-family: 'Fira Sans', sans-serif;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 11px;
  font-weight: 300;
`;

const NavPara = styled.p`
  font-family: 'Fira Sans', sans-serif;
  text-align: left;
  font-weight: 300;
`;
const SearchContainer = styled.div`
  position: relative;
  justifycontent: 'space-between';
  margin-right: 15px;
`;
const FilterInput = styled.input`
  width: 100%;
  border: #e1e1e1 1px solid;
  border-radius: 5px;
  padding: 8px;
  margin-bottom: 15px;
`;
const SearchIcon = {
  position: 'absolute',
  right: '0px',
  top: '5px',
  color: 'black',
};
export const Header = () => (
  <Banner>
    <TopBar>
      <FlexView grow={1}>
        <TopIcon src={NIH} />
        <TopWords>U.S. Department of Health &amp; Human Services </TopWords>
        <TopIcon src={HHS} />
        <TopWords>National Institutes of Health </TopWords>
      </FlexView>
    </TopBar>
    <NavBar>
      <FlexView hAlignContent="right">
        <SearchContainer>
          <FilterInput type="text" />
          <Icon name="search" style={SearchIcon} />
        </SearchContainer>
      </FlexView>
      <FlexView vAlignContent="center" wrap={true}>
        <FlexView marginRight="2em" wrap={true}>
          <NavLink to="/" style={{ textDecoration: 'none', color: 'white' }}>
            <Title>Analytics Hub Header</Title>
          </NavLink>
        </FlexView>
        <FlexView>
          <FlexView marginRight="1em">
            <NavLink
              to="/tools"
              style={{ textDecoration: 'none', color: 'white' }}>
              <NavPara>View All Tools</NavPara>
            </NavLink>
          </FlexView>
          <FlexView marginRight="1em">
            <NavLink to="/" style={{ textDecoration: 'none', color: 'white' }}>
              <NavPara>Tool Collections</NavPara>
            </NavLink>
          </FlexView>
          <FlexView marginRight="1em">
            <NavLink to="/" style={{ textDecoration: 'none', color: 'white' }}>
              <NavPara>CDAC Custom Analysis</NavPara>
            </NavLink>
          </FlexView>
        </FlexView>
      </FlexView>
    </NavBar>
  </Banner>
);



