import * as React from 'react';
import styled from 'styled-components';
import { Icon } from 'react-fa';
import FlexView from 'react-flexview';
// import { Title } from '../../Shared/Title/Title';
const FilterBox = styled.div`
  background-color: #f7f7f7;
  width: 100%;
  padding: 15px;
`;
const FilterTitle = styled.h2`
  font-family: 'Fira Sans', sans-serif;
  font-size: 22px;
  text-align: left;
`;
const FilterInput = styled.input`
  width: 90%;
  float: left;
  border: #e1e1e1 1px solid;
  border-radius: 5px;
  padding: 8px;
  margin-bottom: 10px;
`;
const FilterSectionTitle = styled.h3`
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 600;
  text-align: left;
  margin-bottom: 10px;
`;
const SearchContainer = styled.div`
  position: relative;
`;
const SearchIcon = {
  position: 'absolute',
  right: '15px',
  top: '5px',
};

const FilterSelect = styled.div`
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  margin-bottom: 5px;
`;

const fakeData = [
  { name: 'option item1' },
  { name: 'option item2' },
  { name: 'option item3' },
  { name: 'option item4' },
];

const ButtonLine = styled.div`
  width: 100%;
  height: 0px;
  background-color: #c49000;
  position: absolute;
  bottom: 0;
  left: 0;
`;

const Submit = styled.button`
  position: relative;
  width: 100%;
  margin-top: 10px;
  border-radius: 0;
  border: none;
  padding: 10px;
  background-color: #fbc02d;
  font-family: 'Fira Sans', sans-serif;
  font-size: 14px;

  &:hover {
    cursor: pointer;
  }

  &:hover ${ButtonLine} {
    height: 5px;
    transition: all 0.5s ease;
  }
`;

export const Filters = () => (
  <FilterBox>
    <FilterTitle>Filters</FilterTitle>
    <SearchContainer>
      <FilterInput type="text" />
      <Icon name="search" style={SearchIcon} />
    </SearchContainer>
    <FilterSectionTitle>Section Title</FilterSectionTitle>
    {fakeData.map(i => (
      <FlexView column={false}>
        <FilterSelect>
          <input key="" name="isGoing" type="checkbox" value={i.name} />
          {i.name}
        </FilterSelect>
      </FlexView>
    ))}
    <FilterSectionTitle>Section Title</FilterSectionTitle>
    {fakeData.map(i => (
      <FlexView column={false}>
        <FilterSelect>
          <input key="" name="isGoing" type="checkbox" />
          {i.name}
        </FilterSelect>
      </FlexView>
    ))}

    <Submit>
      Refine Search<ButtonLine />
    </Submit>
  </FilterBox>
);



