import * as React from 'react';
import FlexView from 'react-flexview';
import { Title } from '../../Shared/Title/Title';
import { ToolCard } from '../../Shared/Card/ToolCard/ToolCard';
import Collection from '../../Shared/Card/Collection/Collection';
import { Filters } from '../../Shared/Filter/Filter';
import { FilterTag } from '../../Shared/FilterTag/FilterTag';
import styled from 'styled-components';

const fakeData = [1, 2, 3, 4, 5];
const CollectionContainer = styled.div`
  position: relative;
  min-width: 250px;
  min-height: 280px;
`;
const tags = ['Part A', 'Build Case', 'Collection Here'];

// const styles = {
// cardWidth: {
//   width: '100%'
// }
// };

export const ViewAll = () => (
  <FlexView wrap={true}>
    <FlexView
      marginTop={15}
      marginRight={15}
      marginLeft={15}
      wrap={true}
      grow={1}
      basis={150}>
      <Filters />
    </FlexView>
    <FlexView
      column={true}
      grow={6}
      marginLeft={10}
      marginRight={10}
      basis={400}>
      <FilterTag />
      <Title title="Hello World" />
      <FlexView column={false} wrap={true}>
        {fakeData.map(() => (
          <FlexView key="" marginRight={15} marginBottom={15}>
            <ToolCard
              imageUrl="http://via.placeholder.com/190x80"
              summary=""
              tags={tags}
            />
          </FlexView>
        ))}
      </FlexView>

      <Title title="Hello World Again" />
      <FlexView column={false} wrap={true}>
        {fakeData.map(() => (
          <FlexView key="" marginRight={10} marginBottom={0}>
            <CollectionContainer>
              <Collection />
            </CollectionContainer>
          </FlexView>
        ))}
      </FlexView>
    </FlexView>
  </FlexView>
);



